body {
  margin: 0;
  height: 100%;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.side-navigation {
    height: 100%;
    width: 20.5%;
}

.side-nav-link {
    color: yellow;
    text-decoration: none;
}

.list {
    color: rgb(241, 206, 91);
    margin-left: 100px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.components-wrapper {
    height: 400px;
    width: 800px;
    background-color: orange;
}

.history-section {
    min-height: 500px;
    width: 100%;
}

.nariai-section {
    background-color: rgb(241, 206, 91);
    opacity: 0.7;
    min-height: 500px;
    width: 100%;
    
}

.map-section {
    width: 100%;
    min-height: 500px;
    margin-bottom: 40px;
}

.title {
    margin-left: 20px;
}

.title-yellow {
    color: rgb(241, 206, 91);
    margin-left: 20px;
}

.white-text {
    color: white;
    margin-left: 20px;
}

.black-text {
    margin-left: 20px;
}

.nario-vardas {
    color: black;
}
html {
  height: 100%;
}

.App-logo {
  height: 40vmin;
  width: 35vmin;
}

.ribu-zemelapis {
  margin: 20px;
}

.burelio-nariai {
  width: 900px;
}

.galerijos-nuotrauka {
  /* width: 800px; */
}

.content-wrapper {
  align-items: left;
  color: rgb(42, 43, 40);;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: rgb(79,136,248);
}

#root {
  height: 100%;
}
.content-wrapper {
    width: 100%;
    background-image: url(/static/media/camouflage.9fb97e5d.jpg);
    min-height: 500px;

    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    }

.main-content {
    background: #9e9a75;
    display: inline-block;
    width: 100vw;
    min-height: 100vw;
    text-align: center;

}

.nav-container {
    background:  rgb(241, 206, 91);
    display: flex;
    padding: 2vmin;
    opacity: 0.85;
}

.nav-link {
    color: rgb(42, 43, 40);
    font-size: 2vmin;
    padding: 5vw;
    text-decoration: none;
}

.nav-link:active, .nav-link:visited {
    color: #41533b;
}

p {
    margin: 0;
}
.social-media-footer {
    background: rgb(76, 82, 42);
    width: 100vw;
    position: fixed;
    bottom: 0;
    height: 44px;
    opacity: 0.85;
    color: white;   

}

.social-media-list {
    color:  rgb(241, 206, 91);
    text-align: right;
    margin: 0;
    font-size: 2vmin;
}

.social-media-link {
    display: inline-block;
    padding: 10px;
}
